
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


export default {
  components: {
    Group,
    Cell,
    Blur,
    PopupPicker,
  },
  computed:{
    currentLang:{
      get:function(){
        const locale = this.$store.state.i18n.locale;
        if(locale === 'zh-CN'){
          return ['中文']
        }else if(locale === 'en'){
          return ['English']
        }
        return null
      }
    }
  },
  data () {
    return {
      headImg:require("../assets/headpart.jpg"),
      langs: [['中文', 'English']],
    }
  },
  methods: {
    onChange (val) {
      if(val[0]==='中文'){
        this.$i18n.set('zh-CN')
      }else if(val[0]==='English'){
        this.$i18n.set('en')
      }
     },
  },
}



