import {mapState} from 'vuex'

export default {
    data() {
     return {
      zoom: 9,
      minZoom: 1,
      maxZoom: 18,
      url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      attribution: 'brandon.xiang',
      title: 'test-marker',
      opacity: 1,
      draggable:false
    };
  },
  computed: mapState({
      center: state => state.app.coordinate,
  }),
}




