
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


export default {
  components: {
    Tabbar,
    TabbarItem
  },
  computed:{
    items:function(){
      return [{
        link: "/",
        logo: require("../assets/map.png"),
        name: this.$t('Map')
      },{
        link: "/discovery",
        logo: require("../assets/discovery.png"),
        name: this.$t('Discovery')
      },{
        link:"/setting",
        logo: require("../assets/setting.png"),
        name: this.$t('Setting')
      }];
    }
  },
  methods:{
    itemclick(){
      console.log(1)
    }
  }
}



